#root{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
.main_div{
  width:100%;
  height: 100vh;
  background: #6983aa;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.center_div{
  height: 80vh;
  width: 25%;
  border-radius: 15px;
  display: flex;
   flex-direction: column;
  box-shadow: 5px 5px 25px -5px rgba(0, 0, 0, 0.5);
  background-color: #f4f4f4;
  /* align-items: center; */
}
